<template>
  <div class="goods-item-wrap">
    <div class="goods-item">
      <div class="goods-img">
        <img :src="goods.imgurl || '/static/img/default-bg.jpg'" alt="">
      </div>
      <div class="infos">
        <div class="title">{{goods.name}}</div>
        <div class="sku-info">{{goods.options}}</div>
      </div>
      <div class="price-wrap">
        <div class="price">
          <span class="dollar">￥</span><span class="main-price">{{goods.price}}</span>
        </div>
        <div class="count">
          x{{goods.num}}
        </div>
        <div class="count">消耗积分:{{goods.integral}}</div>
      </div>
    </div>
    <div class="address-cont" @click="selectCard">
      <div class="cont">
        <div class="w-2">代金券</div>
        <div class="f-l">{{ selected || goods.allow.length ? '请选择卡券' : '暂无卡券'}}</div>
        <i data-v-7bb5a5d2="" aria-hidden="true" class="fa fa-chevron-right icon" style="line-height: 2;color: #ddd;"></i>
      </div>
    </div>
    <!-- 卡券列表开始 -->
    <mt-popup
      position="bottom"
      style="height: 50%; width: 100%; z-index: 99999;"
      v-model="cardListViewState">
      <card-list :cardList="goods.allow" :carID="goods.detail_id"></card-list>
    </mt-popup>
    <!-- 卡券列表结束 -->
  </div>
</template>

<script>
import CardList from './card-list.vue'
import bus from '../../../assets/js/bus'
export default {
  name: 'Mall-Order-Confirm-Goods-Item',
  props: {
    goods: Object
  },
  data () {
    return {
      selected: '',
      cardListViewState: false
    }
  },
  mounted () {
    bus.$on('selectCoupon', (card, ID) => {
      if (this.goods.detail_id === ID) {
        this.selected = card.name
      }
    })
  },
  methods: {
    selectCard () {
      this.cardListViewState = true
    }
  },
  components: {
    CardList
  }
}
</script>

<style lang="less" scoped>
  .goods-item-wrap{
    .goods-item{
      display: flex;
      box-sizing: border-box;
      padding: 9px;
      flex-wrap: wrap;
      background-color: #f5f5f5;
      .goods-img{
        width: 92px;
        height: 92px;
        display: block;
        position: relative;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .infos{
        flex: 1;
        padding-left: 5px;
        max-width: 100%;
        display: block;
        position: relative;
        .title{
          line-height: 1.5;
          color: #666;
        }
        .sku-info{
          color: #999;
          font-size: 12px;
        }
      }
      .price-wrap{
        min-height: 20px;
        font-weight: bolder;
        color: #f50;
        .dollar{
          padding: 0 2px;
          font-size: 12px;
          text-align: right;
        }
        .main-price{
          font-size: 16px;
        }
        .sub-price{
          font-size: 12px;
        }
        .count{
          font-size: 12px;
          font-weight: 500;
          text-align: right;
          color: #999;
        }
      }
    }
    .address-cont{
      padding: .6rem;
      text-align: left;
      border-bottom: 1px solid #f4f4f4;
      position: relative;
      .cont{
        display: flex;
        font-size: 15px;
        line-height: 2;
        position: relative;
        .w-2{
          width: 4rem;
          color: #333;
        }
        .f-l{
          flex: 1;
          color: rgba(0, 0, 0, 0.87);
        }
      }
    }
  }
</style>



