<template>
  <div class="item-wrap" @click="selectAddr">
    <div class="cont">
      <span class="name">{{addr.address_name}}</span>
      <span class="addr">{{addr.addressfull}}</span>
    </div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
export default {
  name: 'Cart-Addr-Item',
  props: {
    addr: ''
  },
  beforeDestory () {
    bus.$off('selectOneAddress')
  },
  methods: {
    selectAddr () {
      bus.$emit('selectOneAddress', this.addr)
    }
  }
}
</script>

<style lang="less" scoped>
  .item-wrap{
    padding: .133rem .32rem;
    background-color: #fff;
    border-bottom: 1px solid #eee;
    .cont{
      margin: .2rem 0;
      .name{
        float: left;
        margin: 0;
        font-size: 15px;
        margin-right: 1rem;
        vertical-align: middle;
      }
      .addr{
        font-size: 12px;
        color: #999;
        margin-bottom: 1rem;
        vertical-align: middle;
      }
    }
  }
</style>



