<template>
  <div>
    <div class="card-wrap" v-if="cardList.length">
      <card-item v-for="(item, index) in cardList" :key="index" :card="item" :ID="carID"></card-item>
    </div>
    <div v-else class="notCard">
      <h3>您当前还没有卡券可以使用！</h3>
      <router-link to="/mall/goods/all">立即购买</router-link>
    </div>
  </div>
</template>

<script>
import CardItem from './card-item.vue'
export default {
  name: 'Order-Card-List',
  props: {
    cardList: '',
    carID: ''
  },
  data () {
    return {}
  },
  components: {
    CardItem
  }
}
</script>

<style lang="less" scoped>
  .card-wrap{
    box-sizing: border-box;
    .card-item{
      padding: .4rem;
      border-bottom: 1px solid #ddd;
    }
  }
  .notCard{
    text-align: center;
  }
</style>



