<template>
  <div class="cart-item" @click="selectCard">
    <span>{{card.labels_name + ':'}}</span>
    {{card.name}}
    <i v-show="showCheck" class="fa fa-check" aria-hidden="true" style="color: green;"></i>
  </div>
</template>

<script>
import * as types from '../../../store/mutation-types'
import bus from '../../../assets/js/bus'
import {mallOrderGetPrice} from '../../../api/mall'
import {getLocalStorage} from '../../../lib/util'
export default {
  name: 'Order-Card-Item',
  props: {
    card: Object,
    ID: ''
  },
  data () {
    return {
      showCheck: false
    }
  },
  beforeDestory () {
    bus.$off('selectCoupon')
  },
  methods: {
    selectCard () {
      if (this.showCheck) {
        let obj = {}
        obj.id = this.card.id
        obj.labels = this.card.labels
        obj.detial_id = this.ID
        let _a = this.$store.getters.getUseCardList
        let _b = _a.indexOf(_a)
        this.$store.commit(types.REMOVE_USE_CARD, _b)
        this.showCheck = !this.showCheck
      } else {
        let obj = {}
        obj.id = this.card.id
        obj.labels = this.card.labels
        obj.detial_id = this.ID
        let _a = this.$store.getters.getUseCardList
        let a = null
        if (_a.length <= 0) {
          this.$store.commit(types.ADD_USE_CARD, obj)
          this.showCheck = !this.showCheck
          bus.$emit('selectCoupon', this.card, this.ID)
          // 拼接使用卡券字符串`"${this.ID}":"${this.card.token}"`
          this.$store.commit(types.SET_USE_CARD_OBJECT, {key: this.ID, val: this.card.token})
          mallOrderGetPrice(getLocalStorage('detail'), JSON.stringify(this.$store.getters.getUseCardObject))
            .then(data => {
              if (!data.state) return this.$toast(data.errormsg || '网络错误')
              this.$store.commit(types.SET_ORDER_INFO_PRICE, data.data.info.total_fee)
            })
          return
        }
        // store里存的数据
        _a.forEach((item, index) => {
          if (item.detial_id === this.ID) {
            if ((item.id === this.card.id || item.labels === this.card.labels)) {
              a = true
            }
          } else if ((item.id === this.card.id)) {
            a = true
          }
        })
        if (a) return
        this.$store.commit(types.ADD_USE_CARD, obj)
        this.showCheck = !this.showCheck
        this.$store.commit(types.SET_USE_CARD_OBJECT, {key: this.ID, val: this.card.token})
        mallOrderGetPrice(getLocalStorage('detail'), JSON.stringify(this.$store.getters.getUseCardObject))
          .then(data => {
            if (!data.state) return this.$toast(data.errormsg || '网络错误')
            this.$store.commit(types.SET_ORDER_INFO_PRICE, data.data.info.total_fee)
          })
        bus.$emit('selectCoupon', this.card, this.ID)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .cart-item{
    padding: .4rem;
    border-bottom: 1px solid #eee;
  }
</style>



