<template>
  <div class="main">
    <div class="title" @click="newAddr">新增收货地址</div>
    <cart-addr-item
      v-for="(item, index) in addrs"
      :key="index"
      :addr="item"
    ></cart-addr-item>
  </div>
</template>

<script>
import CartAddrItem from './cart-addr-item.vue'
import bus from '../../../assets/js/bus'
export default {
  name: 'Cart-Address-Popup',
  props: {
    addrs: ''
  },
  beforeDestory () {
    bus.$off('createNewAddress')
  },
  methods: {
    newAddr () {
      bus.$emit('createNewAddress')
    }
  },
  components: {
    CartAddrItem
  }
}
</script>

<style lang="less" scoped>
  .main{
    .title{
      padding: .6rem;
      background-color: cornflowerblue;
      color: #fff;
    }
  }
</style>



