<template>
  <div class="add-wrap">
    <app-header
      title="添加收货地址"
      showBack="false"
      close="true"
    ></app-header>
    <div class="add-main">
      <mt-field label="收货人" placeholder="请输入收货人" v-model="address_name"></mt-field>
      <mt-field label="联系电话" placeholder="请输入联系电话" v-model="address_tel"></mt-field>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">选择省市</span></div>
          <div class="mint-cell-value">
            <select style="min-width: 4rem;" v-model="addressProvince">
              <option
                v-for="(item, index) in city"
                :key="index"
                :value="item.value"
              >{{item.label}}</option>
            </select>
            <select style="min-width: 4rem;" v-model="address_city">
              <option
                v-for="(item, index) in shiList"
                :key="index"
                :value="item.value">
                {{item.label}}
              </option>
            </select>
          </div>
        </div>
      </a>
      <mt-field label="详细地址" placeholder="请输入详细地址" v-model="address"></mt-field>
      <mt-field label="邮政编码" placeholder="请输入邮政编码" v-model="address_code"></mt-field>
      <mt-button type="primary" plain size="large" style="margin-top: 3rem;" @click="addAddr">添加地址</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import SHENGSHI from '../../../../static/json/shengshi.json'
import bus from '../../../assets/js/bus'
import {createAddress} from '../../../api/home'
export default {
  name: 'Vehicle-Add',
  data () {
    return {
      address_name: '',
      address_tel: '',
      address_code: '',
      address: '',
      addressProvince: '',
      address_city: '',
      shiList: '',
      city: SHENGSHI
    }
  },
  watch: {
    addressProvince: function (val) {
      SHENGSHI.forEach((item, index) => {
        if (item.label === val) {
          this.$set(this, 'shiList', SHENGSHI[index].children)
        }
      })
    }
  },
  methods: {
    addAddr () {
      createAddress(this.address_name, this.address_tel, this.address_code, this.address, this.addressProvince, this.address_city)
        .then(data => {
          if (data.state) {
            bus.$emit('addressSuccess')
            this.$toast('添加成功')
          } else {
            alert(data.errormsg)
          }
        })
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .add-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .add-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
    }
  }
</style>



