<template>
  <div class="confirm-wrap">
    <app-header
      title="确认订单"
    ></app-header>
    <div class="confirm-main">
      <div class="address-cont" @click="selectAddress">
        <div class="cont">
          <div class="w-2">收货地址</div>
          <div class="f-l">{{ address ||'请选择收货地址'}}</div>
          <i data-v-7bb5a5d2="" aria-hidden="true" class="fa fa-chevron-right icon"></i>
        </div>
      </div>
      <div class="goods-wrap">
        <goods-item v-for="(item, index) in goodsList" :key="index" :goods="item"></goods-item>
      </div>
      <mt-field label="买家留言" placeholder="留言" type="textarea" rows="4" v-modal="leaveAMessage"></mt-field>
    </div>
    <!--  -->
    <mt-popup
      v-model="popupVisible"
      position="bottom"
      style="height: 50%; width: 100%; z-index: 99999;"
    >
      <cart-address-popup
        :addrs="addressList"
      ></cart-address-popup>
    </mt-popup>
    <mt-popup
      style="height: 100%; width: 100%; z-index: 99999;"
      v-model="addAddressViewState">
      <address-add></address-add>
    </mt-popup>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import GoodsItem from './goods-item.vue'
import CartAddressPopup from '../cart/cart-address-popup.vue'
import AddressAdd from '../../home/address/address-add.vue'
import bus from '../../../assets/js/bus'
import {getAddressList} from '../../../api/home'
import {getLocalStorage, setLocalStorage} from '../../../lib/util'
import {orderCreate} from '../../../api/mall'
import * as types from '../../../store/mutation-types'
export default {
  name: 'Confirm',
  data () {
    return {
      cardListViewState: false,
      addAddressViewState: false,
      popupVisible: false,
      addressList: [],
      goodsList: [],
      selectCard: [],
      address: '',
      addressID: '',
      orderInfo: '',
      leaveAMessage: ''
    }
  },
  mounted () {
    // 增加新的收货地址
    bus.$on('createNewAddress', () => {
      this.addAddressViewState = true
    })
    // 点击关闭模态框
    bus.$on('clickCloseModal', () => {
      this.addAddressViewState = false
    })
    // 增加收货地址成功
    bus.$on('addressSuccess', () => {
      this.getAddr()
      this.addAddresnsViewState = false
    })
    // 选择某个收货地址
    bus.$on('selectOneAddress', addr => {
      this.$set(this, 'address', addr.addressfull)
      this.$set(this, 'addressID', addr.address_id)
      this.popupVisible = false
    })
    // 点击提交订单
    bus.$on('clickSubmitOrder', () => {
//      if (!this.address) return this.$toast('请先选择您的收货地址')
      orderCreate(getLocalStorage('detail'), this.addressID, this.leaveAMessage, JSON.stringify(this.$store.getters.getUseCardObject))
        .then(data => {
          if (!data.state) return this.$toast(data.errormsg || '网络错误')
          setLocalStorage('orderInfos', JSON.stringify(data.data.info))
          this.$router.replace('/mall/pay/?kjljkjklj')
        })
    })
  },
  created () {
    this.getAddr()
    this.orderInfo = JSON.parse(getLocalStorage('orderInfo'))
    this.goodsList = this.orderInfo.mall_list
    let PriceInfo = {}
    PriceInfo.count = this.orderInfo.info.total_amount
    PriceInfo.price = this.orderInfo.info.total_fee
    this.$store.commit(types.SET_ORDER_INFO, PriceInfo)
//    console.log(this.orderInfo)
  },
  methods: {
    selectAddress () {
      this.popupVisible = true
    },
    getAddr () {
      getAddressList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'addressList', data.data.list)
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
    }
  },
  components: {
    AppHeader,
    GoodsItem,
    CartAddressPopup,
    AddressAdd
  }
}
</script>

<style lang="less">
  .confirm-wrap{
    height: 100%;
    background: #fff;
    .confirm-main{
      width: 100%;
      position: absolute;
      padding-top: 40px;
      overflow: hidden;
      &:after {
        content: " ";
        height: 5rem;
        display: block;
      }
      .address-cont{
        padding: .6rem;
        text-align: left;
        border-bottom: 1px solid #f4f4f4;
        position: relative;
        .cont{
          display: flex;
          font-size: 13px;
          position: relative;
          .w-2{
            width: 4rem;
            color: #333;
          }
          .f-l{
            flex: 1;
            color: rgba(0, 0, 0, 0.87);
          }
        }
      }
    }
  }
</style>



